// ë°˜ë‚©ì˜ˆì •ì¼ ê³„ì‚°
function dueDate(rentDate) {
  let date = new Date(rentDate);
  date.setDate(date.getDate() + 7);

  let duedate =
    date.getFullYear() +
    "-" +
    String(Number(date.getMonth()) + 1) +
    "-" +
    date.getDate();

  return duedate;
}

export default MyRentBooks;
