import React, { useState } from "react";

function SeeMoreText({ review }) {

    let [seeMore, setSeeMore] = useState(false);

    return (
        <div>
            {
                seeMore
                    ? review
                    : review.slice(0, 50)
            }
            {
                review.length > 50
                    ? (
                        !seeMore
                            ? <span onClick={() => setSeeMore(true)}>... [ë”ë³´ê¸°]</span>
                            : <span onClick={() => setSeeMore(false)}>[ë‹«ê¸°]</span>
                    )
                    : null
            }
        </div>
    );
}

export default SeeMoreText;